import { InferActionsTypes } from './reduxStore';

export type initialStateType = {
  dialogItems: {
    id: number;
    name: string;
  }[];

  messageItems: {
    id: number;
    message: string;
  }[];

  messageText: string;
};

let initialState: initialStateType = {
  dialogItems: [
    { id: 1, name: '' },
    { id: 2, name: '' },
    { id: 3, name: '' },
    { id: 4, name: '' },
    { id: 5, name: '' },
  ],
  messageItems: [
    { id: 1, message: 'yoo' },
    { id: 2, message: ' ' },
    { id: 3, message: '  ' },
  ],
  messageText: '',
};

export default function dialogsReducer(
  state = initialState,
  action: ActionTypes,
): initialStateType {
  switch (action.type) {
    case 'ADD_MESSAGE': {
      return {
        ...state,
        messageItems: [
          ...state.messageItems,
          {
            id: 6,
            message: action.newMessage,
          },
        ],
      };
    }

    default:
      return state;
  }
}

type ActionTypes = InferActionsTypes<typeof actions>;

export const actions = {
  addMessage: (newMessage: string) =>
    ({
      type: 'ADD_MESSAGE',
      newMessage,
    }) as const,
};
